   <template>
    <div id="db">
        <div id="dbs">
            <select>
   <option v-for="(option,index) in options" v-bind:key="index" v-bind:value="option.dbName">
    {{ option.dbName }}
  </option> </select>
        </div>
        <table class="layui-hide" id="tables"></table>
    </div>
</template>

          
  <script>
export default {
  name: "dbTable",
  data() {
    return {
      tableData: {},
      options: []
    };
  },
  mounted: function() {
    this.loadTables();
    this.getSelectData();
  },
  methods: {
    loadTables: function() {
      layui.use("table", function() {
        var table = layui.table;
        table.render({
          elem: "#tables",
          url: "/dbs/tables",
          cellMinWidth: 80, //全局定义常规单元格的最小宽度，layui 2.2.1 新增
          cols: [
            [
              { field: "dbName", title: "数据库名称", minWidth: 50 }, //minWidth：局部定义当前单元格的最小宽度，layui 2.2.1 新增
              { field: "tableName", title: "数据表名称", minWidth: 50 },
              { field: "tableType", title: "数据表类型", minWidth: 50 },
              { field: "engine", title: "引擎", minWidth: 50 },
              { field: "rows", title: "数据行数", minWidth: 50 }
            ]
          ]
        });
      });
    },
    getSelectData: function() {
      this.axios
        .get("/dbs/list")
        .then(function(res) {
          console.log(res);
          this.options = res.data.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    }
  }
};
</script>


